import '../stylesheets/LeftContainer.css';
import Loader from './Loader';

const LeftContainer = ({data, setUrl, unit, setUnit, lang, setLang}) =>{

  const handleUnit = (e) =>{
    let unit = e.target.name;
    setUnit(unit);
    let newUrl = `https://api.openweathermap.org/data/2.5/weather?q=${data.name},${data.sys.country},&units=${unit}&lang=${lang}&appid=85e6260138afe51f5023631de975e424`
    setUrl(newUrl);
  }
  const handleLanguage = (e)=>{
    let lang = e.target.value.toLowerCase();
    setLang(lang);
    let newUrl = `https://api.openweathermap.org/data/2.5/weather?q=${data.name},${data.sys.country},&units=${unit}&lang=${lang}&appid=85e6260138afe51f5023631de975e424`
    setUrl(newUrl)
  }
  return(
    <div className='LeftContainer'>
      {data ? (
      <>
        <div className='LeftContainer-top'>
        <div className='LeftContainer-top__unitContainer'>
          <input className={unit === 'metric' ? 'unitActive' : 'unit'} type='button' name='metric' value='C' onClick={handleUnit}/>
          <input className={unit === 'imperial' ? 'unitActive' : 'unit'} type='button' name='imperial' value='F' onClick={handleUnit} />
        </div>

        <div className='LeftContainer-top__languageContainer'>
          <input className={lang === 'en' ? 'langActive' : 'lang'} type='button'  value='EN' onClick={handleLanguage}/>
          <input className={lang === 'es' ? 'langActive' : 'lang'} type='button' value='ES' onClick={handleLanguage} />
        </div>
      </div>

      <div className='LeftContainer-bottom'>
        <div className='LeftContainer-bottom__info'>
          <span id='temp'>{Math.trunc(data.main.temp)}</span>

          <div className='city'>
            <p id='cityName'>{data.name}</p>
          </div>

          <div className='weather'>
            <img className='weatherIcon' src={`/images/${data.weather[0].icon}.png`}/>
            {data.weather[0].description}
          </div>
        </div>
      </div>
      </>
      ) 
      : 
      ('')}
      
    </div>
  );
}
export default LeftContainer;