import React, { useEffect, useState } from 'react';
import LeftContainer from './LeftContainer';
import RightContainer from './RightContainer';
import '../stylesheets/WeatherApp.css';
import initialFetch from '../scripts/initialFetch';

const units = {
  metric:[
    {
      temp:'C',
      humidiy: '%',
      pressure: 'Hpa',
      visibility: 'm',
      wind: 'm/s'
    }
  ],
  imperial:[
    {
      temp:'F',
      humidiy: '%',
      pressure: 'Hpa',
      visibility: 'm',
      wind: 'mi/h'
    }
  ]
}

const translations = {
  es:[
    {
      inputPlaceholder: 'Ciudad, Pas',
      title: 'Detalles del Clima',
      feelsLike: 'Sensacin Trmica',
      minTemp: 'Temp. Mnima',
      maxTemp: 'Temp. Mxima',
      humidity: 'Humedad',
      pressure: 'Presin',
      visibility: 'Visibilidad',
      wind: 'Viento'
    }
  ],
  en:[
    {
      inputPlaceholder:'City, Country',
      title: 'Weather Details',
      feelsLike: 'Feels Like',
      minTemp: 'Min. Temp',
      maxTemp: 'Max. Temp',
      humidity: 'Humidity',
      pressure: 'Pressure',
      visibility: 'Visibility',
      wind: 'Wind'
    }
  ],
}

const WeatherApp = ()=>{

  const [url, setUrl] = useState(null);
  const [unit, setUnit] = useState('metric'); //por defecto, utilizo la unidad metrica para el tiempo.
  const [data, setData] = useState(null);
  const [lang, setLang] = useState('es');

/*Este useEffect se ejecutara cuando el endPoint sea modificado por el usuario. EJ: cambio de unidad C a F, cambio de Ciudad/Pais*/
 useEffect(()=>{
   data /* este condicional decide si se ejecutara el fetch inicial o el fetch con los datos ingresados por el usuario.*/
   ?
   //Se ejecuta si hay datos en location
   (
     fetch(url)
     .then(res =>{
       if(!res.ok) throw new Error(res.statusText);
       return res.json();
      })
      .then(data =>{
      setData(data);
    })
    .catch(console.err) 
    )
    :
    //Se ejecuta si no hay datos en location
    (
      initialFetch(setData ,unit, lang)
    ) 
  },[url]); 

      return(
        <div className={data ?  (` ${data.weather[0].icon[2]}-${data.weather[0].main} WeatherApp`) : ('WeatherApp')}>
          <LeftContainer data={data} setUrl={setUrl} unit={unit} setUnit={setUnit} units={units} lang={lang} setLang={setLang}/>
          <RightContainer data = {data} unit={unit} setUrl={setUrl} units={units} lang={lang} translations={translations}/>
        </div>
      );
}

export default WeatherApp;