const key = '85e6260138afe51f5023631de975e424';
const initialFetch = (setData ,unit, lang)=>{

    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    };

    const success = async(pos) =>{ //en caso de que el usuario acepte la geolocalizacion, el fetch se har con la ubicacin del usuario.
      let lat = pos.coords.latitude;
      let lon = pos.coords.longitude;
      const url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&units=${unit}&lang=${lang}&appid=${key}`;
      fetch(url)
    .then(res =>{
      if(!res.ok) throw new Error(res.statusText);
      return res.json();
    })
    .then(data =>{
      setData(data);
    })
    .catch(console.err)
    }

    const error = (err) => { //en caso de que el usuario no acepte la ubicacion, el fetch se har con parametros predeterminados.

      const url = `https://api.openweathermap.org/data/2.5/weather?q=Pergamino,ar&units=${unit}&lang=${lang}&appid=${key}`
      fetch(url)
      .then(res =>{
        if(!res.ok) throw new Error(res.statusText);
        return res.json();
      })
      .then(data =>{
        setData(data);
      })
      .catch(console.err)
      
    };
  
    navigator.geolocation.getCurrentPosition(success, error, options);
  }

  export default initialFetch;