import React, { useState } from 'react';
import Loader from './Loader';
import '../stylesheets/RightContainer.css';
import Capitalize from '../scripts/Capitalize';
const lookup = require('country-code-lookup');

const  date = new Date()
const RightContainer = ({data, unit, setUrl, units, lang, translations}) =>{
  const [location, setLocation] = useState('');
  
  const handleChange = (e)=>{
    setLocation(e.target.value);
  }
  const handleSubmit = (e) =>{
    e.preventDefault();
    const loc = location.split(/ ?[,.-]+/);
    const city = Capitalize(loc[0]);
    let country = Capitalize(loc[1]);
    country = lookup.byCountry(country.iso2);
    let newUrl= `https://api.openweathermap.org/data/2.5/weather?q=${city},${country},&units=${unit}&lang=${lang}&appid=85e6260138afe51f5023631de975e424`
    setUrl(newUrl);
  }

  return(
    <div className='RightContainer'>
      <form onSubmit={handleSubmit} className='serach-location'>
        <input onChange={handleChange} type='search' name='location' placeholder={translations[lang][0].inputPlaceholder}/>
        <i onClick={handleSubmit} className="fas fa-search fa-2x"></i>
      </form>

      {data ? (
      <div className='RightContainer-weather'>
        <h6>{translations[lang][0].title}</h6>
        <div className='RightContainer-weather__details'>
          <div><span>{translations[lang][0].feelsLike}</span> <span>{Math.trunc(data.main.feels_like+" ")} {units[unit][0].temp}</span></div>
          <div><span>{translations[lang][0].minTemp}</span> <span>{Math.trunc(data.main.temp_min+" ")} {units[unit][0].temp}</span></div>
          <div><span>{translations[lang][0].maxTemp}</span> <span>{Math.trunc(data.main.temp_max+" ")} {units[unit][0].temp}</span></div>
          <div><span>{translations[lang][0].humidity}</span> <span>{data.main.humidity+" "}{units[unit][0].humidity}%</span></div>
          <div><span>{translations[lang][0].pressure}</span> <span>{data.main.pressure+" "}{units[unit][0].pressure}</span></div>
          <div><span>{translations[lang][0].visibility}</span> <span>{data.visibility+" "} {units[unit][0].visibility}</span></div>
          <div><span>{translations[lang][0].wind}</span> <span>{data.wind.speed+" "} {units[unit][0].wind}</span></div>
        </div>
      </div>
    ) 
    : 
  (<Loader />)}  
  </div>
  );
}
export default RightContainer;
